body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.big-yosk {
  width: 100%;
}

html {
  height: 100%;

}

body {
  height: 100%
}

#root {
  height: 100%;
}

.ant-tabs .ant-tabs-left-bar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.App {
  font-family: 'Inconsolata', monospace;
  display: -webkit-flex;
  display: flex;
  height: 100%;
  -webkit-justify-content: center;
          justify-content: center;
}

.particles {
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #FFF;
  background-image: url("");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.nav-bar-details span {
  margin-right: 15px;
}

.span.hljs-keyword:nth-child(10) {
  display: block;
}
.hljs {
  overflow: scroll;
}

.ant-table td { white-space: nowrap; }

.nav-bar-details {
  font-family: 'Inconsolata', monospace !important;
}

.ant-layout { background: transparent }
.ant-layout-footer {
  background: rgba(230, 230, 230, 0.5);
  padding: 5px 20px;
  box-shadow: 0 0 6px 0 grey;
}

.ant-input { color: black; }

.ant-input-number-input { color: black; }

.Response > div:nth-child(2), .MemoryProfiler > div:nth-child(2){
  overflow: scroll;
}

.ant-tabs .react-monaco-editor-container {
  min-height: 700px;
}

.ant-tabs {
  height: 100%;
  overflow: scroll;
}

.ant-tabs .ant-tabs-left-bar .ant-tabs-tab {
  text-align: left;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.CustomCard {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    box-shadow: 0px 0px 4px 2px #bbbbbb;
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.82);
    overflow: hidden;
}

.Title {
    font-weight: bold;
}

.Result {
    height: 100%;
    width: 100%;
    background: rgba(255, 255, 255, 0.79);
}

.UpperLayout {
    height: 48%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.LowerLayout {
    height: 48%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.MemoryProfiler {
    overflow: auto;
}

.MemoryProfiler-text {
    font-family: 'Inconsolata', monospace;
    font-size: 14px;
}

.Yosk-form {
    position: absolute; /* or absolute */
    top: 50%;
    left: 50%;
    margin-top: -100px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    overflow: hidden;
    max-height: 600px;
    min-width: 500px;
    min-height: 500px;
}

.input-padding {
    margin: 15px 0;
}



